  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.mosaic-gd-theme .mosaic-window {
  border-radius: 0 !important;
}

.mosaic-gd-theme .mosaic-window-body {
  display: flex;
}

/* Mosaic window and tile */
