.mosaic-gd-theme.mosaic.mosaic-blueprint-theme .mosaic-preview {
  box-shadow: none;
}

.mosaic-gd-theme .mosaic-tile {
  margin: 2px;
  margin-top: 0;
  margin-bottom: 0;
}

.mosaic-gd-theme .mosaic-window-toolbar {
  border-top-right-radius: 0 !important;
  border-top-left-radius: 0 !important;
}

.mosaic-gd-theme .draggable .mosaic-window-title {
  cursor: move;
}

